import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const styles = theme => ({
  root: {
    width: '100%',
    overflowX: 'hidden',
  },
  table: {
    width: '100%',
  },
});

let id = 0;
function createData(dag, datum, feestdag, dienst) {
  id += 1;
  return { id, dag, datum, feestdag, dienst };
}

const rows = [
  createData('ma', '1 apr', '', 'ochtend'),
  createData('di', '2 apr', '', 'ochtend'),
  createData('wo', '3 apr', '', 'ochtend'),
  createData('do', '4 apr', '', 'middag'),
  createData('vr', '5 apr', '', 'middag'),
  createData('vr', '6 apr', '', 'middag'),
  createData('zo', '7 apr', '', ''),
  createData('za', '8 apr', '', ''),
  createData('ma', '9 apr', '', 'ochtend'),
  createData('di', '10 apr', '', 'ochtend'),
  createData('wo', '11 apr', '', 'ochtend'),
  createData('do', '12 apr', '', 'middag'),
  createData('vr', '13 apr', '', 'middag'),
  createData('vr', '14 apr', '', 'middag'),
  createData('zo', '15 apr', '', ''),
  createData('za', '16 apr', '', ''),
  createData('ma', '17 apr', '', 'ochtend'),
  createData('di', '18 apr', '', 'ochtend'),
  createData('wo', '19 apr', '', 'ochtend'),
  createData('do', '20 apr', '', 'middag'),
  createData('vr', '21 apr', '', 'middag'),
  createData('vr', '22 apr', '', 'middag'),
  createData('zo', '23 apr', '', ''),
  createData('za', '24 apr', '', ''),
  createData('ma', '25 apr', '', 'ochtend'),
  createData('di', '26 apr', '', 'ochtend'),
  createData('wo', '27 apr', '', 'ochtend'),
  createData('do', '28 apr', '', 'middag'),
  createData('vr', '29 apr', '', 'middag'),
  createData('vr', '30 apr', '', 'middag'),
  createData('zo', '5 apr', '', ''),
  createData('za', '5 apr', '', ''),
];

function MeiMaand(props) {
  const { classes } = props;

  return (
    <Paper className={classes.root}>
      <Table className={classes.table}>
        <TableHead>
          <TableRow>
            <TableCell padding="dense" align="left">Dag</TableCell>
            <TableCell padding="dense" align="left">Datum</TableCell>
            <TableCell padding="dense" align="left">ATV</TableCell>
            <TableCell padding="dense" align="left">Dienst</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map(row => (
            <TableRow key={row.id}>
              <TableCell padding="dense" align="left" component="th" scope="row">
                {row.dag}
              </TableCell>
              <TableCell padding="dense" align="left">{row.datum}</TableCell>
              <TableCell padding="dense" align="left">{row.feestdag}</TableCell>
              <TableCell padding="dense" align="left">{row.dienst}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </Paper>
  );
}

MeiMaand.propTypes = {
  classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(MeiMaand);
